#projectheader img {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

#projectheader {
    position: relative;
}

#projectheader-text {
    position: absolute;
    top: 45%;
    left: 5%;
    margin: 0;
}

#projectheader-text h2 {
    margin: 0;
    padding-bottom: 5px;
    font-size: 32pt;
    color: #5A5A5A;
    text-decoration: underline;
    transition: 0.25s color linear;
}

#projectheader-text p {
    margin: 0;
    font-size: 14pt;
    color: #5A5A5A;
}

#projectheader-text:hover  {
    cursor: default;
}

#projectheader-exit {
    position: fixed;
    top: 2%;
    left: 2%;
}

#projectheader-exit a {
    text-decoration: none;
    color: #5A5A5A;
    font-size: 28pt;
    transition: 0.25s color linear;
}

#projectheader-exit:hover a {
    color: #6E9E69;
}