.proj, .proj p, .proj a {
    background-color: transparent;
}
#projects h2 {
    color: #F9F9F9;
    position: relative;
    text-align: center;
    font-size: 36pt;
    background: transparent;
    z-index: 1;
}

.proj {
    width: 100%;
    position: relative;
}
.proj .projImg {
    width: 100%;
    border-radius: 20px;
}

.desc {
    background-color: transparent;
    position: absolute;
    max-width: 40%;
    top: 40%;
    left: 5%;
    text-align: left;
    z-index: 1;
}

#projectsSection .proj .desc h2 {
    text-decoration: none;
    color: #5A5A5A;
    font-size: 24pt;
    text-align: left;
    margin: 0;
    padding-bottom: 0px;
    border-bottom: 1px solid transparent;
    transition: 0.5s all linear;
}

#projectsSection .proj .desc p {
    color: #5A5A5A;
    margin: 0;
    font-size: 14pt;
}

#projectsSection .proj:hover .desc h2 {
    color: #6E9E69;
    border-bottom: 1px solid #6E9E69;
}

.projLinkCard {
    width: 90vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}

/* begin media queries */