.resume {
    width: 25em;
    height: 32.5em;
    background-color: #F9F9F9;
    border: 1pt solid #5A5A5A;
    border-radius: 20px;
    box-shadow: 2px 2px 0.5px rgba(90, 90, 90, 0.1);
}
.resume a {
    text-decoration: none;
}
.resume:hover {
    box-shadow: -0.5px -0.5px #858585;
    background-color: rgba(90, 90, 90, 0.1);
}
.resume p {
    background-color: #6E9E69;
    text-align: center;
    color: #F9F9F9;
    padding: 1em 0 1em 0;
    border-radius: 0 0 19px 19px;
    font-size: 16pt;
    position: relative;
    top: -0.88em;
    left: -1px;
    width: 100%;
    border: 1pt solid #5A5A5A;
}
.resume img {
    width: 90%;
    position: relative;
    left: 1.25em;
    top: 3em;
    border: 0.5pt solid #5A5A5A;
    filter: grayscale(100%);
    transition: all 0.5s;
}
.resume:hover img {
    filter: none;
    top: 2em;
    box-shadow: 2px 2px 0.5px rgba(90, 90, 90, 0.2);
}
.resume:last-of-type {
    position: relative;
    top: 15em;
}
/* begin media queries */
@media screen and (max-width: 1100px) {
    body {
        max-width: 100vw;
    }

    /* index page */
    .resume:first-of-type {
        top: 0;
    }
    .resume:last-of-type {
        top: 5em;
    }
}

@media screen and (max-width: 600px) {
    .resume {
        left: 7%;
    }
}