.icon a {
    text-decoration: none;
    color: #5A5A5A;
    font-size: 14pt;
    text-align: center;
}
.icon {
    position: relative;
    display: inline-block;
    text-align: left;
    margin: 1.5em 0.5em;
}
.icon .icontext {
    visibility: hidden;
    width: 150px;
    background-color: #6E9E69;
    color: #F9F9F9;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 90%;
    left: 105%;
    margin-left:-60px;
    opacity: 0;
    transition: opacity 0.25s;
}
.icon .icontext::after {
    content: "";
    position: absolute;
    top: 90%;
    left: 15%;
    margin-left: -5px;
    border-width: 10px;
    border-style: solid;
    border-color: #6E9E69 transparent transparent transparent;
}
/* .icon:hover .icontext {
    visibility: visible;
    opacity: 1;
} */
.icon svg {
    margin-right: 0.4em;
}
svg {
    color: #5A5A5A;
    transition: 0.25s all;
}
.icon:hover svg {
    color: #6E9E69;
    transform: rotateZ(10deg) scale(1.05);
    margin-right: 0.5em;
}
.icon:hover {
    cursor: pointer;
}
.icon:hover a {
    color: #6E9E69;
}