#top {
  min-height: 101vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#top h1 {
  color: #5A5A5A;
  font-family: "Baskerville Old Face";
  font-size: 42pt;
  text-align: center;
  position: relative;
  margin-bottom: 0.5em;
}

#projectsSection {
    height: auto;
    padding-bottom: 4em;
    padding-top: 4em;
    background-color: #5A5A5A;
}
#projectsSection h2 {
    color: #F9F9F9;
    position: relative;
    font-size: 36pt;
    margin-left: 5%;
    background: transparent;
}
#ddpList {
    background-color: transparent;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}
#ddpList li {
    display: inline-block;
    margin: 0.5em;
    font-size: 20pt;
    color: #F9F9F9;
    background-color: inherit;
    transition: 0.3s all;
}
#ddpList li:hover {
    cursor: pointer;
    color: #6E9E69;
}
.active {
    text-decoration: underline;
}
.design, .development, .programming {
    background-color: #5A5A5A;
}

#about {
    margin-top: 3em;
    margin-left: auto;
    margin-right: auto;
    width: 90vw;
}
#about p {
    color: #5A5A5A;
    font-size: 16pt;
    margin-bottom: 2em;
    margin-left: 2em;
}
#about h2 {
    font-size: 26pt;
    margin-bottom: 1em;
}
#about h3, #resumes h3 {
    font-size: 18pt;
    margin-bottom: 1em;
}
#about h2, #resumes h3 {
    color: #6E9E69;
}
#about h3 {
    color: #5A5A5A;
}
#contact {
    margin-bottom: 5em;
}
#resumes {
    max-width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-bottom: 25em;
}

.icons {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.type-selected {
    text-decoration: underline;
}

.left, .right {
    float: left;
    margin-bottom: 6em;
    margin-top: 6em;
}
.left {
    width: 65%;
}
.right {
    width: 35%;
}

/* begin media queries */
@media screen and (max-width: 1100px) {
    #resumes {
        padding-bottom: 10em;
    }
    #triag {
        bottom: 0;
    }

    #projects h2 {
        top: 1.2em;
    }
}

@media screen and (max-width: 600px) {
    /* remove buttons (yikes) */
    .circles {
        display: none;
    }
    
    #about p {
        margin-left: 0;
    }

    #projects h2 {
        font-size: 18pt;
    }
}